@import url(https://fonts.googleapis.com/css2?family=Inter:wght@100..900);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
    font-family: 'Inter', sans-serif;
    font-weight: 200;
}
p {
    font-size: 30px;
    text-align: center;
}
.flex-container {
    display: flex;
    justify-content: center;
}

#button {
    cursor: pointer;
    text-align: center;
    background-color: #542bff;
    border-radius: 10px;
    width: 150px;
    height: 50px;
    font-size: 20px;
}

#button:hover {
    cursor: pointer;
    text-align: center;
    background-color: #fabc30;
    border-radius: 10px;
    width: 150px;
    height: 50px;
    font-size: 20px;
    /*  Making button bigger on hover  */
    transform: scale(1.1) perspective(1px)
}
.clock {
    color: red;
}


@-webkit-keyframes scaleTitle {
    60% {
        font-size: 120px;
        font-weight: 100;
    }
    100% {
        font-size: 125px;
        font-weight: 300;
    }
}

@keyframes scaleTitle {
    60% {
        font-size: 120px;
        font-weight: 100;
    }
    100% {
        font-size: 125px;
        font-weight: 300;
    }
}

.titleWrapper {
    text-align: center;
}

.title {
    font-size: 125px;
    font-weight: 300;
    -webkit-animation-name: scaleTitle;
            animation-name: scaleTitle;
    -webkit-animation-duration: 3s;
            animation-duration: 3s;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
}

.titleWrapper span > span:nth-child(even) {
    color: #542bff;
}

.nav-bar {
    display: flex   ;
    justify-content: space-between;
    margin: 20px 40px;
    padding: 0 0 20px 0 ;
    border-bottom: 1px solid black;
}

a {
    /*Remove underline for a tag*/
    text-decoration: none;
    color: black;
}

.main-menu {
    display: flex;
}

.menu-item {
    margin: 5px;
    display: inline;
    font-size: 20px;
    padding: 5px;
    margin: 0 20px;
}

.site-logo {
    font-size: 40px;
    font-weight: 700;
    margin: 5px 30px;
}

.header-bar {
    display: block;
    justify-content: center;
}




.social-widget {
    display: flex;
    align-items: center;
    margin: 5px 30px;
}

